export function friendlyDate(datsStr) {
  let dateObj = typeof datsStr === "object" ? datsStr : new Date(datsStr);
  let time = dateObj.getTime();
  let now = Date.now();
  let period = now - time;
  let str = "";

  switch (true) {
    case period < 60000:
      str = "åˆšåˆš";
      break;
    case period < 1000 * 3600:
      str = Math.floor(period / 60000) + "åˆ†é’Ÿå‰";
      break;
    case period < 1000 * 3600 * 24:
      str = Math.floor(period / (1000 * 3600)) + "å°æ—¶å‰";
      break;
    default:
      str = Math.floor(period / (1000 * 3600 * 24)) + "å¤©å‰";
  }
  return str;
}



